.input {
  padding: 12px 20px;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  max-width: 300px;
  font-size: 16px;
  padding-left: 40px;

  &:focus {
    border: 1px solid #fe5d1e77;
  }
}
.wrapper {
  position: relative;
}
.img {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 11px;
  left: 10px;
  transition: 0.2s ease-in-out;

  &:hover {
    transform: scale(1.1);
    transition: 0.1s ease-in-out;
  }
}
.close {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 14px;
  right: 10px;
  cursor: pointer;
  opacity: 0.6;
  transition: 0.2s ease-in-out;
  &:hover {
    opacity: 0.9;
    transition: 0.2s ease-in-out;
    width: 21px;
    height: 21px;
  }
}
