@import '../variables.scss';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 950px) {
      flex-direction: column;
      gap: 15px;
      .header__logo {
        margin-left: 25px;
      }

      .header__cart {
        margin-bottom: 20px;
      }
    }
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
      transition: 0.2s ease-in-out;
      &:hover {
        transform: rotate(180deg);
        transition: 0.3s ease-in-out;
      }
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
