@import './variables.scss';
@import './libs/normalize.scss';
@import './components/all.scss';

body {
  background-color: $background;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;
  @media (max-width: 645px) {
    width: calc(100vw - 30px);
    margin-top: 20px;
  }
}

.content {
  padding: 40px 0;

  &__title {
    margin: 35px 0;
    font-weight: 800;
    @media (max-width: 730px) {
      text-align: center;
    }
  }

  &__items {
    margin: 0 auto;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 11px;
    grid-row-gap: 20px;
    flex-wrap: wrap;
    @media (max-width: 1255px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1000px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 730px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1250px) {
      flex-direction: column-reverse;

      .sort {
        margin-bottom: 20px;
      }
    }
  }
}

.container {
  width: $container-width;
  margin: 0 auto;
  padding: 0 10px;

  &--cart {
    max-width: 820px;
    margin: 90px auto;
    .content__title {
      margin: 0;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 540px) {
      flex-direction: column;
      align-items: center;
      row-gap: 20px;
    }
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }
  .content__items {
    display: block;
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;
    @media (max-width: 540px) {
      flex-direction: column;
      align-items: center;
    }

    &-img {
      display: flex;
      align-items: center;
      margin-right: 20px;
      width: 10%;
      @media (max-width: 540px) {
        margin: 0;
        justify-content: center;
      }

      img {
        width: 80px;
        height: 80px;
        @media (max-width: 540px) {
          width: 140px;
          height: 140px;
        }
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 30%;
      margin-right: 30px;
      @media (max-width: 898px) {
        margin: auto;
      }
      @media (max-width: 540px) {
        width: 100%;
        text-align: center;
      }

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
        @media (max-width: 898px) {
          font-size: 18px;
        }
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
        @media (max-width: 898px) {
          font-size: 16px;
        }
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 15%;
      gap: 10px;
      @media (max-width: 898px) {
        width: 10%;
        margin-right: 10px;
      }
      @media (max-width: 540px) {
        width: 30%;
        text-align: center;
        margin-bottom: 30px;
      }

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }
        &:active {
          transform: none;
        }
        &:disabled {
          opacity: 0.3;
          border-color: #787878;
          svg path {
            fill: #787878;
          }
          &:hover {
            background-color: transparent;
            svg path {
              fill: #787878;
            }
          }
        }

        @media (max-width: 898px) {
          width: 22px;
          height: 22px;
          min-width: 22px;
        }
      }
      &-plus {
        @media (max-width: 898px) {
          width: 22px;
          height: 22px;
          min-width: 22px;
        }
      }

      b {
        font-size: 22px;
        @media (max-width: 898px) {
          font-size: 18px;
        }
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33%;
      @media (max-width: 898px) {
        width: 30%;
      }

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
        @media (max-width: 898px) {
          font-size: 18px;
        }
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 4%;
      @media (max-width: 540px) {
        text-align: center;
        justify-content: center;
      }

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
        @media (max-width: 898px) {
          width: 22px;
          height: 22px;
          min-width: 22px;
        }
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
        @media (max-width: 540px) {
          font-size: 15px;
          margin-right: 10px;
          text-align: center;
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;
      @media (max-width: 540px) {
        flex-direction: column;
        row-gap: 20px;
        align-items: center;
      }

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
      }
    }
  }

  &--empty {
    margin: 0 auto;
    max-width: 560px;
    text-align: center;

    h2 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 240px;
      margin: 45px auto 60px;
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}
.content__error-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 100px auto;
  width: 500px;

  h2 {
    font-size: 32px;
  }
  p {
    font-size: 20px;
  }
}
.fullPizza {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  img {
    object-fit: cover;
    width: 60%;
  }
  h2 {
    margin-bottom: 10px;
    @media (max-width: 430px) {
      font-weight: 14px;
    }
  }
  p {
    margin-bottom: 15px;
  }
}
