.paginate {
  li {
    display: inline-block;

    a {
      padding: 5px 12px;
      border-radius: 10px;
      border: 1px solid #fe5f1e;
      margin-right: 10px;
      text-align: center;
      color: #fe5f1e;
      cursor: pointer;
      transition: 0.2s ease-in-out;
      &:hover {
        background-color: #fe5f1e;
        transition: 0.2s ease-in-out;
        color: azure;
      }
    }
  }
  :global {
    .selected {
      a {
        background-color: #fe5f1e;
        color: azure;
      }
    }
  }
}
